.gpt4__blog{
    display: flex;
    flex-direction: column;
}
.gpt4__blog-heading{
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
}

.gpt4__blog-heading h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;

    letter-spacing: -0.04em;
}

.gpt4__blog-container{
    display: flex;
    flex-direction: row;
}

.gpt4__blog-container_groupA{
    flex: 0.75;
    margin-right: 2rem;
}

.gpt4__blog-container_groupB{
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
}

@media screen and (max-width: 990px){
    .gpt4__blog-container{
        flex-direction: column;
    }
    .gpt4__blog-container_groupA{
        margin:2rem 0;
    }
    .gpt4__blog-container_groupA .gpt4__blog-container_article{
        width: 100%;
    }
    .gpt4__blog-container_groupA .gpt4__blog-container_article-image{
        height: 250px;
        }
    
}

@media screen and (max-width: 700px){
    .gpt4__blog-container_groupB{
        grid-template-columns: 1fr;
    }
    .gpt4__blog-heading{
        margin-bottom: 2rem;
    }
    .gpt4__blog-heading h1{
        font-size: 42px;
        line-height: 50px;
    }
    
}
@media screen and (max-width: 550px){
    .gpt4__blog-heading h1{
        font-size: 34px;
        line-height: 42px;   
    }
    
}